import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import styled from 'styled-components';
import Divider from '../core/Divider';
import AnimatedText from '../components/AnimatedText';

const ContentInner = styled.div``;
const StyledName = styled.div`
  font-weight: 800;
  margin: 0;
`;
const StyledSubtitle = styled.div`
  font-size: 2rem;
`;

function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={(data) => {
        const { author, role } = data.site.siteMetadata;

        return (
          <section>
            <ContentInner>
              <div>
                <StyledSubtitle>Hello, I am</StyledSubtitle>
                <StyledName>{author}</StyledName>
                <StyledSubtitle>{role}</StyledSubtitle>
              </div>
              {/* <AnimatedText author={author} role={role} /> */}
            </ContentInner>
            <Divider className="stripe teal" />
          </section>
        );
      }}
    />
  );
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        role
        social {
          linkedin
          github
        }
      }
    }
  }
`;

export default Bio;
