import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  max-width: 800px;
  position: relative;
  display: block;
  margin-bottom: 15px;
  width: 100%;
  background: #eee;
  height: 35px;
  border-radius: 3px;
  transition: 0.4s linear;
  transition-property: width, background-color;
`;
const Title = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  font-weight: bold;
  font-size: 13px;
  color: #ffffff;
  background: ${props => `linear-gradient(to right, #f09819, #ff512f ${props.score}%)`};
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  width: ${props => `${props.score}%`};
`;
const Name = styled.span`
  display: block;
  width: 110px;
  background: rgba(0, 0, 0, 0.1);
  padding: 0 20px;
  height: 35px;
  line-height: 35px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
`;
const Bar = styled.div`
  height: 35px;
  width: 0px;
  background: #6adcfa;
  border-radius: 3px;
`;

function Skillbar({
  className, name, score, background,
}) {
  return (
    <React.Fragment>
      <Wrapper className={className} data-percent="20%">
        <Title score={score} background={background}>
          <Name>{name}</Name>
        </Title>
        <Bar style={{ background: '#e67e22' }} />
        {/* <Percentage>{score}</Percentage> */}
      </Wrapper>
    </React.Fragment>
  );
}

export default Skillbar;
